import { BehaviorSubject } from 'rxjs';
import { SyndicateTransport, TGetProjectInfoResponse } from '../transport';

type TProjectServiceData = {
  data: TGetProjectInfoResponse | void;
  error: Error | void;
};

export class ProjectService {
  private static initialData: TProjectServiceData = { data: void 0, error: void 0 };

  data$ = new BehaviorSubject<TProjectServiceData>(ProjectService.initialData);

  constructor(private transport: SyndicateTransport) {}

  getProjectInfo(id: string): void {
    void this.transport.getProjectInfo(id)
      .then((data: TGetProjectInfoResponse) => this.data$.next({ data, error: void 0 }))
      .catch((error: Error) => this.data$.next({ data: void 0, error }));
  }

  reset(): void {
    this.data$.next(ProjectService.initialData);
  }
}

