import { TGetProjectInfoResponse } from 'common';
import {
  CopyRight,
  EmptyBlock,
  Layout,
  LayoutContent,
  Text,
  ContentPageLayout,
  ContentPageLayoutList
} from 'components';
import React from 'react';
import {
  projectInfoStyle,
  projectPhotoStyle
} from './project-page.style';

type TProjectPageContentProps = {
  project: TGetProjectInfoResponse;
};

export function ProjectPageContent({ project }: TProjectPageContentProps): JSX.Element {
  return (
    <Layout footerContent={<CopyRight />} isContentFullLayoutView={false}>
      <LayoutContent height="auto">
        <ContentPageLayout>
          <div className={projectInfoStyle}>
            <Text size="l">{project.name}</Text>
            <EmptyBlock height="40px" />
            <Text size="l">{project.description}</Text>
            <EmptyBlock height="60px" />
            <ContentPageLayoutList>
              <Text>{project.year}</Text>
              <Text>{project.competition}</Text>
              <div>
                <Text>
                  {project.collaborators.length === 0 ? 'private commission' : 'contributors:'}
                </Text>
                {project.collaborators.map((collaborator: string) => (
                  <Text key={collaborator}>{collaborator}</Text>
                ))}
              </div>
            </ContentPageLayoutList>
          </div>
          <div>
            {project.hashtags.map((hashtag: string) => <Text key={hashtag} size="l">{hashtag}</Text>)}
          </div>
        </ContentPageLayout>
      </LayoutContent>
      {project.photos.map((photoUrl: string) => (
        <img
          src={photoUrl}
          key={photoUrl}
          alt={project.name}
          className={projectPhotoStyle}
        />
      )
      )}
    </Layout>
  );
}
