import { fetchImages } from 'common';
import { BehaviorSubject } from 'rxjs';
import { TUserChoiseItem, UserDecisionStorageService } from './user-decision-storage.service';
import { UserSwipe } from '../enums';
import {
  SyndicateTransport,
  TGetUserSendedSelectionResponse,
  TItemModel
} from '../transport';

type TResultsPageData = Record<UserSwipe, Array<TItemModel>>;

type TServiceData = {
  data: TGetUserSendedSelectionResponse | void;
  error: Error | void;
};
export class ResultsPageService {
  private static initialData: TServiceData = { data: void 0, error: void 0 };

  data$ = new BehaviorSubject<TServiceData>(ResultsPageService.initialData);
  state$ = new BehaviorSubject<UserSwipe>(UserSwipe.SELECT);

  constructor(private transport: SyndicateTransport, private storage: UserDecisionStorageService) {}

  getData(selectedId: string | null, rejectedId: string | null): void {
    if (!(selectedId && rejectedId)) {
      const data = this.getDataFromStorage();

      this.data$.next({ data, error: void 0 });

      return;
    }

    void this.transport.getUserSendedSelection(selectedId, rejectedId)
      .then((response: TGetUserSendedSelectionResponse) => {
        const state = this.state$.getValue();

        fetchImages(
          response[state === UserSwipe.SELECT ? UserSwipe.SELECT : UserSwipe.REJECT]
            .map((item: TItemModel) => item.photo.url));

        this.data$.next({ data: response, error: void 0 });
      });
  }

  switchState(state?: UserSwipe): void {
    const prevState = this.state$.getValue();
    this.state$.next(state ? state
      : prevState === UserSwipe.SELECT ? UserSwipe.REJECT : UserSwipe.SELECT);
  }

  getResultsCount(): { selectedCount: number; rejectedCount: number } {
    if (!this.data$.getValue().data) {
      this.getData(null, null);
    }

    return {
      selectedCount: this.data$.getValue().data?.[UserSwipe.SELECT].length || 0,
      rejectedCount: this.data$.getValue().data?.[UserSwipe.REJECT].length || 0
    };
  }

  clearDataFromStorage = (): void => {
    this.storage.clear();
    this.data$.next({ data: { [UserSwipe.REJECT]: [], [UserSwipe.SELECT]: [] }, error: void 0 });
  };

  private getDataFromStorage(): TResultsPageData {
    const data = this.storage.getItems();

    const result: TResultsPageData = {
      [UserSwipe.SELECT]: [],
      [UserSwipe.REJECT]: []
    };

    data.forEach(({ item, decision }: TUserChoiseItem) => {
      result[decision].push(item);
    });


    return result;
  }
}
