import { css } from '@emotion/css';
import { copyrightHeightPX, headerMenuHeightPX } from 'components';
import { Colors } from '../colors';
import { mobileMediaQuery } from '../consts';

export const layoutStyle = (backgroundColor?: Colors, isContentFullLayout?: boolean): string => css({
  width: '100%',
  height: isContentFullLayout ? '100%' : 'auto',
  boxSizing: 'border-box',
  overflow: 'scroll',
  padding: isContentFullLayout ? '0' : `${headerMenuHeightPX}px 0 ${copyrightHeightPX}px`,
  backgroundColor
});

export const layoutContentStyle = (height?: string): string => css({
  width: '100%',
  height,
  boxSizing: 'border-box',
  padding: '0 40px',

  [mobileMediaQuery]: {
    padding: '0 20px'
  }
});
