import { css } from '@emotion/css';
import { fadeInKeyFrames, stepsKeyframes } from 'components';

export const introPageStyle = css({
  width: '100%',
  height: '100%',
  display: 'flex',
  alignItems: 'flex-start',
  justifyContent: 'center',
  flexDirection: 'column'
});

export const headerWrapperStyle = (isOnTop: boolean): string => css({
  transform: isOnTop ? 'translate3d(0, 0, 0)' : 'translate3d(0, 45vh, 0)',
  transition: 'transform 600ms ease-out',
  animation: `${fadeInKeyFrames} 700ms forwards`
});

export const contentStyle = css({
  display: 'flex',
  flexDirection: 'column'
});

export const explanationStyle = css({
  display: 'flex',
  flexWrap: 'wrap'
});

export const stepStyle = (isVisible: boolean): string => css({
  animation: isVisible
  ? `${fadeInKeyFrames} 700ms ease-in forwards, ${stepsKeyframes} 300ms forwards ease-in 400ms`
  : '',
  opacity: 0
});

export const isVisibleStyle = (isVisible: boolean): string => css({
  animation: isVisible ? `${fadeInKeyFrames} 700ms forwards` : '',
  opacity: 0
});
