import { Navigator, resultsPageService, useOnClickOutside } from 'common';
import React from 'react';
import { headerMenuStyle, menuItemsStyle } from './header-menu.style';
import { EmptyBlock } from '../empty-block';
import { Text } from '../text';

type THeaderMenuProps = {
  withMenu?: boolean;
};

export function HeaderMenu({ withMenu = true }: THeaderMenuProps ): JSX.Element {
  const [isMenuOpened, setIsMenuOpened] = React.useState(false);

  const ref = React.useRef<HTMLDivElement | null>(null);

  const handleOpenMenu = React.useCallback(() => setIsMenuOpened(true), [setIsMenuOpened]);
  const handleCloseMenu = React.useCallback(() => setIsMenuOpened(false), [setIsMenuOpened]);

  useOnClickOutside(ref, handleCloseMenu);

  const handleNavigateToLikes = React.useCallback(() => {
    handleCloseMenu();
    Navigator.navigateToLikes();
  }, [handleCloseMenu]);

  const handleNavigateToSecretDesires = React.useCallback(() => {
    handleCloseMenu();
    Navigator.navigateToSecretDesires();
  }, [handleCloseMenu]);

  const handleNavigateToGame = React.useCallback(() => {
    handleCloseMenu();
    Navigator.navigateToGame();
  }, [handleCloseMenu]);

  const handleNavigateToStudio = React.useCallback(() => {
    handleCloseMenu();
    Navigator.navigateToStudio();
  }, [handleCloseMenu]);

  const handleNavigateToAllProjects = React.useCallback(() => {
    handleCloseMenu();
    Navigator.navigateToAllProjects();
  }, [handleCloseMenu]);

  const handleNavigateToContactForm = React.useCallback(() => {
    handleCloseMenu();
    Navigator.navigateToContactForm();
  }, [handleCloseMenu]);

  return (
    <div
      className={headerMenuStyle(isMenuOpened)}
      ref={ref}
    >
      <div>
        <Text color="black" size="l" align="center">Syndicate <br /> Architects</Text>
        {withMenu && (
          <>
            <EmptyBlock height="14px" />
            <Text
              onClick={isMenuOpened ? handleCloseMenu : handleOpenMenu}
              color="black"
              align="center"
            >
              {isMenuOpened ? 'close menu' : 'menu'}
            </Text>
          </>
        )
        }
      </div>
      {isMenuOpened
      && (() => {
        const { selectedCount, rejectedCount } = resultsPageService.getResultsCount();

        return (
          <>
            <div className={menuItemsStyle}>
              <Text
                size="l"
                color="black"
                align="center"
                onClick={handleNavigateToLikes}
              >
                your likes {selectedCount}
              </Text>
              <Text
                size="l"
                color="black"
                align="center"
                onClick={handleNavigateToSecretDesires}
              >
                your secret desires {rejectedCount}
              </Text>
              <Text
                size="l"
                color="red"
                align="center"
                onClick={handleNavigateToGame}
              >
                back to game
              </Text>
            </div>
            <div className={menuItemsStyle}>
              <Text
                size="l"
                color="black"
                align="center"
                onClick={handleNavigateToStudio}
              >
                studio
              </Text>
              <Text
                size="l"
                color="black"
                align="center"
                onClick={handleNavigateToAllProjects}
              >
                all projects
              </Text>
              <Text
                size="l"
                color="black"
                align="center"
                onClick={handleNavigateToContactForm}
              >
                contact us
              </Text>
            </div>
          </>);
      })()}
    </div>
  );
}
