import {
  bottomSheetService,
  resultsPageService,
  TGetUserSendedSelectionResponse,
  TItemModel,
  useNavigatorState,
  UserSwipe
} from 'common';
import {
  CopyRight,
  Layout,
  ProjectItem
} from 'components';
import React from 'react';
import { EmptyResultPage } from './empty-result-page';
import { ResultSwitcher } from './result-switcher';
import { ResultsMenuSheet } from './results-menu-sheet';
import {
  resultItemWrapperStyle,
  resultsPageStyle,
  resultSwitcherWrapperStyle
} from './results-page.style';

type TResultsPageContentProps = {
  data: TGetUserSendedSelectionResponse;
  onClear: VoidFunction | undefined;
};

export function ResultsPageContent({ data, onClear }: TResultsPageContentProps): JSX.Element {
  const { view } = useNavigatorState({ view: UserSwipe.SELECT });

  const handleOpenResultMenuSheet = React.useCallback(() => bottomSheetService.open(
    <ResultsMenuSheet onClear={onClear} />
  ), [onClear]);

  React.useEffect(() => resultsPageService.switchState(view), [view]);

  if (!data[view].length) {
    return <EmptyResultPage view={view} />;
  }

  return (
    <Layout footerContent={<CopyRight onClick={handleOpenResultMenuSheet} />}>
      <div className={resultsPageStyle}>
        {data[view].map((project: TItemModel, index: number) => (
          <div key={index} className={resultItemWrapperStyle}>
            <ProjectItem
              id={project.project}
              projectName={project.projectName}
              imageUrl={project.photo.url}
            />
          </div>
        )
        )}
        <div className={resultSwitcherWrapperStyle}>
          <ResultSwitcher />
        </div>
      </div>
    </Layout>
  );
}
