import React from 'react';
import { contentPageLayoutList } from './content-page-layout.style';

type TContentPageLayoutListProps = {
  children: React.ReactNode;
};

export function ContentPageLayoutList({ children }: TContentPageLayoutListProps): JSX.Element {
  const asArrayChildren = React.Children.toArray(children);

  return (
    <div className={contentPageLayoutList(asArrayChildren.length)}>
      {asArrayChildren.map((child: React.ReactNode, index: number) => <div key={index}>{child}</div>)}
    </div>
  );
}
