import {
  TGetAllProjectsModel,
  TGetItemsModel,
  TGetProjectInfoRequest,
  TGetProjectInfoResponse,
  TGetUserSendedSelectionResponse,
  TSetUserSelectionRequest,
  TSetUserSelectionResponse
} from './models';
import { transport } from '../utils';

export class SyndicateTransport {
  private static ENDPOINT: string =
  'https://script.google.com/macros/s/AKfycbxF8LC0aKhdVN5mFalyNkp9HBcQNidcPTnh89xTmMkxj4o3ZGA-BygHkJ-MJ_q3kbFO/exec';

  getProjectsForSelection(): Promise<TGetItemsModel> {
    return transport.post({
      url: SyndicateTransport.ENDPOINT,
      method: 'get_projects_for_selection'
    });
  }

  getAllProjects(): Promise<TGetAllProjectsModel> {
    return transport.post({
      url: SyndicateTransport.ENDPOINT,
      method: 'get_all_projects'
    });
  }

  getUserSendedSelection(selectedId: string, rejectedId: string): Promise<TGetUserSendedSelectionResponse> {
    return transport.post({
      url: SyndicateTransport.ENDPOINT,
      method: 'get_user_sended_selection',
      params: { selectedId, rejectedId }
    });
  }

  getProjectInfo(projectId: string): Promise<TGetProjectInfoResponse> {
    const params: TGetProjectInfoRequest = { projectId: Number(projectId) };

    return transport.post({
      url: SyndicateTransport.ENDPOINT,
      method: 'get_project_info',
      params
    });
  }

  setUserSelection(data: TSetUserSelectionRequest): Promise<TSetUserSelectionResponse> {
    return transport.post({
      url: SyndicateTransport.ENDPOINT,
      method: 'set_user_selection',
      params: data
    });
  }
}

export const syndicateTransport = new SyndicateTransport();
