export function fetchImagePromise(imageUrl: string): Promise<boolean> {
  return new Promise((resolve: (val: boolean) => void, reject: () => void) => {
    const image = new Image();
    image.onload = () => resolve(true);
    image.onerror = reject;
    image.src = imageUrl;
  });
}

export function fetchImages(images: Array<string>): void {
  images.forEach((imageUrl: string) => void fetchImagePromise(imageUrl));
}
