import React from 'react';
import { contentPageLayoutStyle } from './content-page-layout.style';

type TContentPageLayoutProps = {
  children: React.ReactNode;
};

export function ContentPageLayout({ children }: TContentPageLayoutProps): JSX.Element {
  const asArrayChildren = React.Children.toArray(children);

  return (
    <div className={contentPageLayoutStyle(asArrayChildren.length)}>
      {asArrayChildren.map((child: React.ReactNode, index: number) => <div key={index}>{child}</div>)}
    </div>
  );
}
