import { BehaviorSubject, combineLatest, Subscription } from 'rxjs';
import { FormControl } from '../utils';

const EMAIL_REGEXP = /^[\w.\-_--]+@[\w.\-_--]+\.[\w--]+$/;
const PHONE_REGEXP = /^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4,6}$/im;

enum FormControlErrors {
  NAME_FIELD_EMPTY = 'Enter your name',
  CONTACT_FIELD_EMPTY = 'Enter your email or phone',
  CONTACT_FIELD_EMAIL = 'Check your email is correct',
  CONTACT_FIELD_PHONE = 'Check your phone number',
  EMPTY = ''
}

enum FormErrors {
  EMPTY_CONTROL = 'Please fill these two forms',
  INCORRECT_CONTACT = 'Please fill email or phone correctly'
}

export class UserDataForm {
  private subscribtion: Subscription | void = void 0;

  readonly isFormValid$ = new BehaviorSubject<boolean>(false);
  readonly formError$ = new BehaviorSubject<string>('');

  readonly fields = {
    name: new FormControl<string, FormControlErrors>('', (name: string) => this.validateName(name)),
    contact: new FormControl<string, FormControlErrors>('', (contact: string) => this.validateContact(contact))
  };

  constructor() {
    this.subscribtion = this.validateForm();
  }

  destroy(): void {
    const fields = this.getFields();

    fields.forEach((field: FormControl<string, FormControlErrors>) => field.destroy());

    this.subscribtion?.unsubscribe();
  }

  private validateName(name: string): FormControlErrors {
    if (name.length === 0) return FormControlErrors.NAME_FIELD_EMPTY;

    return FormControlErrors.EMPTY;
  }

  private validateContact(contact: string): FormControlErrors {
    if (contact.length === 0) return FormControlErrors.CONTACT_FIELD_EMPTY;

    if (contact.includes('@')) {
      const isEmailValid = EMAIL_REGEXP.test(contact);

      if (isEmailValid) return FormControlErrors.EMPTY;

      return FormControlErrors.CONTACT_FIELD_EMAIL;
    }

    const isPhoneValid = PHONE_REGEXP.test(contact);

    if (isPhoneValid) return FormControlErrors.EMPTY;

    return FormControlErrors.CONTACT_FIELD_PHONE;
  }

  private validateForm(): Subscription | void {
    const name = this.fields.name;
    const contact = this.fields.contact;

    return combineLatest(
      name.error$, contact.error$
    ).subscribe(([nameError, contactError]: [FormControlErrors, FormControlErrors]) => {
      this.isFormValid$.next(!nameError && !contactError);
      this.formError$.next(this.resolveFormError([nameError, contactError]));
    });
  }

  private getFields(): Array<FormControl<string, FormControlErrors>> {
    return Object.values<FormControl<string, FormControlErrors>>(this.fields);
  }

  private resolveFormError([nameError, contactError]: [FormControlErrors, FormControlErrors]): string {
    if (nameError === FormControlErrors.NAME_FIELD_EMPTY || contactError === FormControlErrors.CONTACT_FIELD_EMPTY) {
      return FormErrors.EMPTY_CONTROL;
    }

    if ([FormControlErrors.CONTACT_FIELD_EMAIL, FormControlErrors.CONTACT_FIELD_PHONE].includes(contactError)) {
      return FormErrors.INCORRECT_CONTACT;
    }

    return '';
  }
}
