import { bottomSheetService, useOnClickOutside, useRx } from 'common';
import React from 'react';
import { bottomSheetStyle } from './bottom-sheet.style';
import { SheetContainer } from '../sheet-container';
import { Text } from '../text';

export function BottomSheet(): JSX.Element {
  const state = useRx(bottomSheetService.state$);
  const ref = React.useRef<HTMLDivElement | null>(null);

  useOnClickOutside(ref, bottomSheetService.close);

  return (
    <SheetContainer bottom>
      <div className={bottomSheetStyle(state.isShow)} ref={ref}>
        {state.component}
        <Text align="center" color="white" onClick={bottomSheetService.close}>close</Text>
      </div>
    </SheetContainer>
  );
}
